import { NextRequest, NextResponse } from "next/server";
import { z } from "zod";
import { getServerAuthSession } from "@/lib/auth";
import { prisma } from "@/lib/db/prisma";

const numberOrZero = z.preprocess(
  (value) => {
    if (value === null || value === undefined || value === "") return 0;
    if (typeof value === "number" && Number.isNaN(value)) return 0;
    return value;
  },
  z.number()
);

const nullableString = z.string().nullable().optional();

const proposalItemSchema = z.object({
  id: z.string().optional(),
  productId: z.string().nullable().optional(),
  name: z.string(),
  description: nullableString,
  qty: numberOrZero,
  price: numberOrZero,
  discount: numberOrZero.optional(),
  unit: z.string().nullable().optional(),
});

const productVariantGroupRowSchema = z.object({
  id: z.string(),
  type: z.literal("group"),
  title: z.string(),
});

const productVariantItemRowSchema = z.object({
  id: z.string(),
  type: z.literal("item"),
  groupId: z.string().nullable().optional(),
  productId: z.string().nullable().optional(),
  name: z.string(),
  description: nullableString,
  qty: numberOrZero,
  price: numberOrZero,
  discount: numberOrZero.optional(),
  unit: z.string().nullable().optional(),
});

const productVariantSchema = z.object({
  id: z.string(),
  name: z.string(),
  description: nullableString,
  isRecommended: z.boolean().optional(),
  currency: z.string().nullable().optional(),
  rows: z.array(z.union([productVariantGroupRowSchema, productVariantItemRowSchema])),
});

const productsViewSchema = z.object({
  showDiscountColumn: z.boolean().optional(),
  showUnitColumn: z.boolean().optional(),
});

const advantageSchema = z.object({
  id: z.string().optional(),
  iconUrl: z.string().nullable().optional(),
  title: z.string(),
  description: z.string(),
});

// Схема валидации для создания/обновления proposal
const proposalSchema = z.object({
  title: z.string().default(""),
  clientId: z.string().nullable().optional(),
  recipientName: nullableString,
  problemDesc: nullableString,
  solutionDesc: nullableString,
  additionalDesc: nullableString,
  items: z.array(proposalItemSchema), // JSON массив
  currency: z.string().default("RUB"),
  pricingMode: z.enum(["single", "variants"]).default("single"),
  productVariants: z.preprocess(
    (value) => (value === null ? undefined : value),
    z.array(productVariantSchema).optional()
  ),
  activeVariantId: z.string().nullable().optional(),
  productsView: z.preprocess(
    (value) => (value === null ? undefined : value),
    productsViewSchema.optional()
  ),
  deadline: nullableString,
  paymentTerms: nullableString,
  paymentCustom: nullableString,
  validUntil: z.preprocess(
    (value) => {
      if (value === "" || value === null || value === undefined) return undefined;
      return value;
    },
    z.union([z.string(), z.date()]).optional()
  ),
  includeVat: z.boolean().default(true),
  vatRate: numberOrZero.default(20),
  galleryImages: z.preprocess(
    (value) => (value === null ? undefined : value),
    z.array(z.string()).optional()
  ),
  advantages: z.preprocess(
    (value) => (value === null ? undefined : value),
    z.array(advantageSchema).optional()
  ),
  advantagesColumns: z.preprocess(
    (value) => {
      if (value === null || value === undefined || value === "") return 3;
      return value;
    },
    z.number().int().min(1).max(3)
  ).default(3),
  visibleSections: z.preprocess(
    (value) => (value === null ? undefined : value),
    z.array(z.string()).optional()
  ),
  ctaText: nullableString,
  ctaPhone: nullableString,
  ctaEmail: nullableString,
  notes: nullableString,
});

const proposalPatchSchema = proposalSchema.partial().extend({
  id: z.string().min(1, "Proposal ID is required"),
  clientId: z.string().nullable().optional(),
});

const parseValidUntil = (value: unknown): Date | null | undefined => {
  if (value === undefined) return undefined;
  if (value === null || value === "") return null;

  const date = new Date(value as string | number | Date);
  return Number.isNaN(date.getTime()) ? null : date;
};

// GET /api/proposals - получить все КП workspace
export async function GET() {
  try {
    const session = await getServerAuthSession();
    if (!session?.user?.workspaceId) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
    }

    const proposals = await prisma.proposal.findMany({
      where: { workspaceId: session.user.workspaceId },
      orderBy: { updatedAt: "desc" },
      include: {
        Client: {
          select: {
            id: true,
            name: true,
            email: true,
          },
        },
      },
    });

    return NextResponse.json(proposals);
  } catch (error) {
    console.error("GET /api/proposals error:", error);
    return NextResponse.json({ error: "Internal server error" }, { status: 500 });
  }
}

// POST /api/proposals - создать новое КП
export async function POST(request: NextRequest) {
  try {
    const session = await getServerAuthSession();
    if (!session?.user?.workspaceId) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
    }

    const body = await request.json();
    const validatedData = proposalSchema.parse(body);
    const validUntil = parseValidUntil(validatedData.validUntil);
    const { clientId, ...validatedWithoutClient } = validatedData;

    const proposal = await prisma.proposal.create({
      data: {
        ...validatedWithoutClient,
        workspaceId: session.user.workspaceId,
        createdBy: session.user.id,
        updatedBy: session.user.id,
        validUntil: validUntil ?? null,
        ...(clientId ? { clientId } : {}),
      },
    });

    return NextResponse.json(proposal, { status: 201 });
  } catch (error) {
    if (error instanceof z.ZodError) {
      return NextResponse.json(
        { error: "Validation error", details: error.issues },
        { status: 400 }
      );
    }

    console.error("[POST /api/proposals] Error:", error instanceof Error ? error.message : String(error));
    return NextResponse.json({ 
      error: "Internal server error",
      message: error instanceof Error ? error.message : String(error)
    }, { status: 500 });
  }
}

// PATCH /api/proposals - обновить существующее КП
export async function PATCH(request: NextRequest) {
  try {
    const session = await getServerAuthSession();
    if (!session?.user?.workspaceId) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
    }

    const body = await request.json();
    const parsed = proposalPatchSchema.safeParse(body);
    if (!parsed.success) {
      return NextResponse.json(
        { error: "Validation error", details: parsed.error.issues },
        { status: 400 }
      );
    }

    const { id, clientId, ...allowedUpdates } = parsed.data;
    const updates = { ...allowedUpdates } as Record<string, unknown>;
    const validUntil = parseValidUntil(updates.validUntil);
    const clientRelation =
      clientId === undefined
        ? undefined
        : clientId
          ? { connect: { id: clientId } }
          : { disconnect: true };

    // Проверяем, что КП принадлежит текущему workspace
    const existing = await prisma.proposal.findFirst({
      where: { id, workspaceId: session.user.workspaceId },
    });

    if (!existing) {
      return NextResponse.json({ error: "Proposal not found" }, { status: 404 });
    }

    const proposal = await prisma.proposal.update({
      where: { id },
      data: {
        ...updates,
        updatedBy: session.user.id,
        ...(validUntil !== undefined ? { validUntil } : {}),
        ...(clientRelation ? { Client: clientRelation } : {}),
      },
    });

    return NextResponse.json(proposal);
  } catch (error) {
    console.error("[PATCH /api/proposals] Error:", error);
    return NextResponse.json({ 
      error: "Internal server error",
      message: error instanceof Error ? error.message : String(error)
    }, { status: 500 });
  }
}
